//Dark Blue (Dark Mode Elements)
$dark-mode-elements: hsl(209, 23%, 22%);

//Very Dark Blue (Dark Mode Background)
$dark-mode-background: hsl(207, 26%, 17%);

//Very Dark Blue (Light Mode Text)
$light-mode-text: hsl(200, 15%, 8%);

//Dark Gray (Light Mode Input)
$light-mode-input: hsl(0, 0%, 52%);

//Very Light Gray (Light Mode Background)
$light-mode-background: hsl(0, 0%, 98%);

//White (Dark Mode Text & Light Mode Elements)
$dark-mode-text: hsl(0, 0%, 100%);
$light-mode-elements: hsl(0, 0%, 100%);


:export {
    darkModeElements: $dark-mode-elements;
    darkModeBackground: $dark-mode-background;
    darkModeText: $dark-mode-text;

    lightModeElements: $light-mode-elements;
    lightModeBackground: $light-mode-background;
    lightModeText: $light-mode-text;
    lightModeInput: $light-mode-input;
    
}